import React, { Component } from 'react';
import { Row, Col, Form, Button, Dropdown } from 'react-bootstrap';
import jwtDecode from 'jwt-decode'
import { hostUrl } from '../../store/config';
import _ from 'lodash'

class childProfile extends Component{

    constructor(props)
    {
        super(props);

        this.state ={
            childForm: false,
            addChild: false,
            msg:'',
            keyForm: null,
            newChild:{
                child_name:'',
                child_gender:'',
                child_dob:''
            },
            userChild: []
        }
    }

    static getDerivedStateFromProps(props, state) {
        if(props.children && props.children.length > 0)
        {
            return {
                userChild : props.children,
                childMsg: props.childMsg,
                variant: props.variant,
                addChild: props.addChildForm,
            }
        }
        else
        {
            return {
                userChild : props.children,
                childMsg: props.childMsg,
                variant: props.variant,
                addChild: props.addChildForm
            }
        }
        
    }

    handleChildFieldChange = (event) => {
        let updatedChild = this.state.newChild;

        switch(event.target.name)
        {
            case 'child_name':
                updatedChild.child_name = event.target.value;
                console.log('Child Name :: ' + updatedChild.child_name);
            break;

            case 'child_gender':
                updatedChild.child_gender = event.target.value;
                console.log('Child Gender :: ' + updatedChild.child_gender);
            break;

            case 'child_dob':
                updatedChild.child_dob = event.target.value;
                console.log('Child DOB :: ' + updatedChild.child_dob);
            break;

            default:
                return  
        }

        this.setState({
            newChild: updatedChild
        })
    }

    handleOpenForm = (event) => {
        let {id} = event.target;
        console.log('EVENT :: ' + id);
        this.setState({
            childForm: !this.state.childForm,
            keyForm: id
        })
    }

    handleUpdateChild= (event) => {
        let newChild = this.state.userChild;
        
        let id = (event.target.name).split('^')[0];
        let name = (event.target.name).split('^')[1];
        console.log('Name :: ' + name);

        newChild.map((child) =>{
            if(child.id == id)
            {
                switch(name)
                {
                    case 'child-name':
                        child.name = event.target.value;
                    break;
        
                    case 'dob':
                        child.dob = event.target.value;
                    break;
        
                    case 'child-gender':
                        child.gender = event.target.value;
                    break;

                    case 'image':
                        child.image = event.target.files[0];
                        console.log('Image :: ' + child.image.name);
                    break;

                }
            }
        })

        this.setState({
            userChild: newChild
        })
//        msg: 'Photo selected, Press save to upload'
    }

    handleChildPhoto = (event) => {
        this.setState({
            image: event.target.files[0],
            msg: 'Photo selected, Press save to upload'
        })
    }

    handleSubmit = (event) => {
        
        let formBody = new FormData();

        if(event.target.getAttribute('dataID'))
        {
            console.log('selected child ID :: ' + event.target.getAttribute('dataID'));
            let id = event.target.getAttribute('dataID');
            let Child = _.find(this.state.userChild, (item) => (item.id === id))

            formBody.append('child_id',Child.id);
            formBody.append('name', Child.name);
            formBody.append('gender', Child.gender);
            formBody.append('dob', Child.dob);
            
            if(typeof Child.image !== 'string')
            {
                formBody.append('image', Child.image);
            }

            this.props.submitChild(formBody, 'UPDATE');
        }
        else
        {
            console.log('This is new child ');
            if(localStorage.getItem('jwtToken'))
            {
                formBody.append('parent_id',(jwtDecode(localStorage.getItem('jwtToken'))).cust_id)
            }
            
            formBody.append('name', this.state.newChild.child_name);
            formBody.append('gender', this.state.newChild.child_gender);
            formBody.append('dob', this.state.newChild.child_dob);
            formBody.append('image', this.state.image);

            this.props.submitChild(formBody, 'ADD');
        }

        this.setState({
            keyForm: null,
            childForm: false,
            newChild:{
                child_name:'',
                child_gender:'',
                child_dob:''
            },
            image:'',
            msg:'',
        })
    }

    handleDelete = (event) => {
            let id = event.target.getAttribute('dataID')
            
            this.props.submitChild(id, 'DELETE');
    }




    render()
    {
        if(this.props.children)
        // userChild = this.props.children
        console.log('PROPS :: ' + JSON.stringify(this.state.userChild));

        return (
            <div id="child">
               
                <div className="head">Child Details 
                    {(this.state.userChild && this.state.userChild.length > 0 ) ? <span
                        onClick={
                            () => {
                                this.props.toggleForm();
                            }
                        }
                    >+ Add Another child</span>:''}
                </div>

                {(this.state.userChild && (!this.state.userChild.length > 0) ) &&
                <div className="note show">Please enter your child details so that we can help in choosing the best products for your child </div>}
                
                <Row className={(this.state.addChild === true) ? "mr-3 ml-3 border-bottom add-child show" : "mr-3 ml-3 border-bottom add-child hide"}>
                    <Col md={7} className="p-0">
                            <Form>
                                <Form.Group as={Row} controlId="child-name" className="mb-4">
                                    <Form.Label column sm={2}>
                                        Name:
                                    </Form.Label>
                                    <Col sm={10}>
                                        <Form.Control type="text" name="child_name" placeholder="Name" value={(this.state.newChild.child_name)} onChange={this.handleChildFieldChange}/>
                                    </Col>
                                </Form.Group>
                                <Form.Group as={Row} controlId="dob" className="mb-4">
                                    <Form.Label column sm={2} className="pr-0">
                                        Date of Birth:
                                    </Form.Label>
                                    <Col sm={7}>
                                        <Form.Control type="date" name="child_dob" value={(this.state.newChild.child_dob)} onChange={this.handleChildFieldChange}/>
                                        <img className="calender-icon" src="/assets/images/calender-icon.png" alt=""/>
                                    </Col>
                                </Form.Group>
                                <Form.Group as={Row} className="gender" controlId="gender">
                                    <Form.Label column sm={2}>
                                    Gender:
                                    </Form.Label>
                                    <Col sm={8}>
                                        <Form.Group as={Row}  onChange={this.handleChildFieldChange}>
                                            <Col sm={5}>
                                                {this.state.newChild.child_gender === 'Male' ?
                                                <Form.Check
                                                    type="radio"
                                                    label="Boy"
                                                    name="child_gender"
                                                    value='Male'
                                                    checked
                                                    id="formHorizontalRadios5"
                                                    />:
                                                <Form.Check
                                                    type="radio"
                                                    label="Boy"
                                                    name="child_gender"
                                                    value='Male'
                                                    id="formHorizontalRadios5"
                                                />}
                                            </Col>
                                            <Col sm={5}>
                                            {this.state.newChild.child_gender === 'Female' ?
                                                <Form.Check
                                                    type="radio"
                                                    label="Girl"
                                                    name="child_gender"
                                                    value='Female'
                                                    checked
                                                    id="formHorizontalRadios6"
                                                />:
                                                <Form.Check
                                                    type="radio"
                                                    label="Girl"
                                                    name="child_gender"
                                                    value='Female'
                                                    id="formHorizontalRadios6"
                                                />}
                                            </Col>
                                            <p style={{color:`${this.state.variant}`}} >{this.state.childMsg}</p>
                                            <Col sm={12}>
                                                <Button className="btn-submit" dataID='' onClick={this.handleSubmit}>Save</Button>
                                            </Col>
                                        </Form.Group>
                                    </Col>
                                </Form.Group>
                            </Form>
                    </Col>
                    <Col md={5} className="p-0">
                        <div className="text-left">
                            <div className="image-container">
                                <img src="/assets/images/boy-placeholder.png" alt=""/>
                            </div>
                            <div className='upload-btn-wrapper show'>
                                <button className="btn-upload">Upload Photo</button>
                                <p>{this.state.msg}</p>
                                <input
                                    onChange={this.handleChildPhoto}
                                    type="file" name="image"
                                />
                            </div>
                        </div>
                    </Col>
                </Row>
                
                {(this.state.userChild && (this.state.userChild.length > 0) ) &&
                    (this.state.userChild.map((child, key) => (
                        <Row className="mr-3 ml-3 border-bottom" key={key}>
                        <Col md={7} className="p-0">
                            <div className={(this.state.childForm === true && key == this.state.keyForm) ? "form show" : "form hide"}>
                                <Form id={child.id}>
                                    <Form.Group as={Row} controlId="child-name" className="mb-4">
                                        <Form.Label column sm={2}>
                                            Name:
                                        </Form.Label>
                                        <Col sm={10}>
                                            {/* <Form.Control type='hidden' value={child.id} /> */}
                                            <Form.Control type="text" name={child.id+'^child-name'} onChange={this.handleUpdateChild}
                                                value={child.name} />
                                        </Col>
                                    </Form.Group>
                                    <Form.Group as={Row} controlId="dob" className="mb-4">
                                        <Form.Label column sm={2} className="pr-0">
                                            Date of Birth:
                                        </Form.Label>
                                        <Col sm={7}>
                                            <Form.Control type="date" name={child.id+'^dob'} onChange={this.handleUpdateChild}
                                                value={child.dob}/>
                                            <img className="calender-icon" src="/assets/images/calender-icon.png" alt=""/>
                                        </Col>
                                    </Form.Group>
                                    <Form.Group as={Row} className="gender" controlId="gender">
                                        <Form.Label column sm={2}>
                                        Gender:
                                        </Form.Label>
                                        <Col sm={8}>
                                            <Form.Group as={Row} onChange={this.handleUpdateChild}>
                                                <Col sm={5}>                          
                                                {child.gender === 'Male' ?
                                                    <Form.Check
                                                        type="radio"
                                                        label="Boy"
                                                        name={child.id+'^child-gender'}
                                                        value="Male"
                                                        checked
                                                        id="formHorizontalRadios5"
                                                        />:
                                                     <Form.Check
                                                        type="radio"
                                                        label="Boy"
                                                        name={child.id+'^child-gender'}
                                                        value="Male"
                                                        id="formHorizontalRadios5"
                                                        />}    

                                                </Col>
                                                <Col sm={5}>
                                                    {child.gender === 'Female' ?
                                                    <Form.Check
                                                        type="radio"
                                                        label="Girl"
                                                        name={child.id+'^child-gender'}
                                                        value="Female"
                                                        checked
                                                        id="formHorizontalRadios6"
                                                    />:
                                                    <Form.Check
                                                        type="radio"
                                                        label="Girl"
                                                        name={child.id+'^child-gender'}
                                                        value="Female"
                                                        id="formHorizontalRadios6"
                                                    />}
                                                    
                                                </Col>
                                                <Col sm={12}>
                                                    <Button className="btn-submit" dataID={child.id} onClick={this.handleSubmit}>Save</Button>
                                                </Col>
                                            </Form.Group>
                                        </Col>
                                    </Form.Group>
                                </Form>
                            </div>
                            <div className={(this.state.childForm === true && (key == this.state.keyForm)) ? "child-list hide" : "child-list show"}>
                                    <div>
                                    <span className="name">{child.name}</span>
                                    <span className="dob"><span>Date of Birth: </span> {child.dob} </span>
                                    <span className="gender"><span>Gender </span> {child.gender} </span>
                                    </div>
                            </div>
                        </Col>
                        <Col md={5} className="p-0">
                            <div className="text-left">
                                <div className="image-container">
                                    <img width='100%' height='100%' src={child.image ? `${hostUrl}${child.image}`:"/assets/images/boy-placeholder.png"} alt=""/>
                                    <span id={key}
                                        onClick={this.handleOpenForm} 
                                        className={(this.state.childForm === true && (key == this.state.keyForm)) ? "bg-edit hide" : "bg-edit show"}
                                    ></span>
                                    <span dataID={child.id}
                                        onClick={this.handleDelete}
                                        className={(this.state.childForm === true && (key == this.state.keyForm)) ? "bg-del hide" : "bg-del show"}
                                    ></span>
                                </div>
                                <div className={(this.state.childForm === true && (key == this.state.keyForm)) ? "upload-btn-wrapper show" : "upload-btn-wrapper hide" }>
                                    <button className="btn-upload">Upload Photo</button>
                                    <p>{this.state.msg}</p>
                                    <input
                                        onChange={this.handleUpdateChild}
                                        type="file" name={child.id+'^image'}
                                    />
                                </div>
                            </div>
                        </Col>
                    </Row>
                    )))
                }
            </div>
        )
    }
    

}

export default childProfile;
